.GWBM_grid {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: grid;
  grid-template-rows: 1fr 56px;
  grid-template-columns: 100%;
}

.GWBM_content {
  height: 100%;
  padding: 1vmin;
  overflow-y: scroll;
}

.GWBM_nav {
  display: flex;
  justify-content: center;
  align-items: center;
}